.top {
    display: flex;
    justify-content: space-between;
}
.logo {
    width: 2rem;
    height: 2rem;
}
h1 {
    font-size: 7rem;
    font-family: monospace;
    background-image: linear-gradient(to right, rgb(48, 245, 48) 10%, rgb(255, 166, 0)50%);
    background-clip: text;
    text-align: center;
    color: transparent;
}

.search {
    text-align: center;
    padding: 2rem;
    margin-top: 5rem;
}
.search input {
    width: 40rem;
    height: 4rem;
    border-radius: 10rem;
    padding: .7rem 3rem;
    font-size: 2.5rem;
    outline: none;
    margin: auto;
    border: 1px solid greenyellow;
    transition: all .3s;
}
.search input::placeholder {
    color: rgb(228, 226, 226);
    font-style: italic;
}
.search input:focus {
    border-color: orange;
    width: 45rem;
}